



import React from 'react';
import { Row, Col, Card, Table, Tabs, Tab, Badge } from 'react-bootstrap';

import Loader from '../../App/layout/Loader/index'

import Jhilikka from '../../App/components/Reusables/Jhilikka';
import DeleteModal from '../../App/components/Reusables/DeleteModal';
import { getGiftCard, deleteGiftCards, deleteUniqueCards } from '../../api/payment/giftCards';
import isEmpty from '../../validation/isEmpty';
import Paging from '../../App/components/Reusables/Paging';
import IsEmpty from '../../App/components/Reusables/IsEmpty';
import Spinner from '../../App/components/Reusables/Spinner';




const GiftCard = (props) => {


    const [giftCardData, setGiftCardData] = React.useState({});
    const [loading, setLoading] = React.useState(true);
    const [deleteGiftCardData, setDeleteGiftCardData] = React.useState({ showModal: false, giftCardUuid: null })
    const [deleteUniqueCardData, setDeleteUniqueCardData] = React.useState({ showModal: false, uniqueCardUuid: null })

    React.useEffect(() => {
        const controller = new AbortController();

        const requestJson = { action: 'get', uuid: props.match.params.id, filter: 'uuid' }
        console.log(requestJson);
        getGiftCard(requestJson, controller.signal)
            .then(async (res) => {
                console.log(res.json)
                if (res.json.status) {
                    await setGiftCardData(res.json.gift_cards[0])
                    setLoading(false)
                } else {
                    Jhilikka.fire({ icon: 'error', title: res.json.error, background: '#ffe5de' })
                }
            }).catch((err) => {
                console.log(err);
            });
        return () => controller.abort();
    }, [])


    const _triggerDeleteGiftCardModal = (e) => {
        setDeleteGiftCardData({ showModal: true, giftCardUuid: props.match.params.id });
    }

    const _triggerDeleteUniqueCardModal = (uuid) => {
        console.log(uuid);
        setDeleteUniqueCardData({ showModal: true, uniqueCardUuid: uuid });
    }




    const _deleteGiftCard = (toBeDeletedUuid) => {
        deleteGiftCards({ action: 'delete', gift_cards_uuid: [toBeDeletedUuid] })
            .then(res => res.json)
            .then((res) => {
                if (res.status) {
                    Jhilikka.fire({ icon: 'success', title: 'Gift Card deleted successfully!', background: '#dffff0' })
                    props.history.push('/gift-cards')
                } else {
                    Jhilikka.fire({ icon: 'error', title: res.json.error, background: '#ffe5de' })
                }
            }).catch((err) => {
                console.log(err);
            });
    }
    const _deleteUniqueCard = (toBeDeletedUuid) => {
        console.log(toBeDeletedUuid);
        deleteUniqueCards({ action: 'delete', uuids: [toBeDeletedUuid] })
            .then(async (res) => {
                console.log(res)
                if (res.json.status) {
                    setDeleteUniqueCardData({ showModal: false, placementId: null })
                    Jhilikka.fire({ icon: 'success', title: 'Unique Card deleted successfully!', background: '#dffff0' })
                    props.history.push('/gift-card/' + props.match.params.id)
                } else {
                    Jhilikka.fire({ icon: 'success', title: res.json.error, background: '#ffe5de' })
                }
            }).catch((err) => {
                console.log(err);
            });
    }

    const UniqueCardsList = giftCardData.unique_cards ? giftCardData.unique_cards.map((card, index) => {
        return (
            <tr key={index}>
                <th scope="row">{card.id}</th>
                <td>{card.code}</td>
                <td>{card.is_used ? 'Yes' : 'NO'}</td>
                <td>{card.is_active ? 'Yes' : 'No'}</td>
                <td>
                    <i
                        className="feather icon-trash-2 text-c-red f-19 m-r-5"
                        style={{ cursor: 'pointer' }}
                        onClick={() => _triggerDeleteUniqueCardModal(card.uuid)}
                    />
                </td>
            </tr>
        )
    }) : null

    if (loading) return <Loader />

    return (
        <>
            <Row className="d-flex justify-content-between pageHead" >
                <h4 className="m-b-10" >{giftCardData.name}</h4>
                <div>
                    <button
                        className="btn btn-primary btn-sm shadow-2 mb-4"
                        onClick={() => props.history.push(`/gift-card/update/${props.match.params.id}`)}
                    > <i className="feather icon-edit text-c-white f-15 m-r-5" />Update</button>
                    <button
                        className="btn btn-danger btn-sm shadow-2 mb-4"
                        onClick={_triggerDeleteGiftCardModal}
                    > <i className="feather icon-trash-2 text-c-black f-15 m-r-5" />Delete</button>
                </div>
            </Row>
            <Row>
                <Col xl={1}></Col>
                <Col xl={10}>
                    <Card>
                        <Card.Body>
                            <h5 className='mb-4'>Gift Card Details</h5>
                            <div className="row d-flex align-items-center">

                                <Col md={2}></Col>
                                <Col md={8}>
                                    <div className='mb-1 border-bottom'><b>Name</b></div>
                                    <div className='mt-1 mb-3'>
                                        {giftCardData.name}{" "}
                                        {giftCardData.is_active ? (<Badge variant="info">Active</Badge>) : (<Badge variant="secondary">Inactive</Badge>)}
                                    </div>

                                    <Row>
                                        <Col md={6}>
                                            <div className='mb-1 border-bottom'><b>Category</b></div>
                                            <div className='mt-1 mb-3'>{giftCardData.category_str}</div>
                                        </Col>
                                        <Col md={6}>
                                            <div className='mb-1 border-bottom'><b>Code</b></div>
                                            <div className='mt-1 mb-3'>{giftCardData.code}</div>
                                        </Col>
                                    </Row>
                                    <Row>
                                        <Col md={6}>
                                            <div className='mb-1 border-bottom'><b>Rate</b></div>
                                            <div className='mt-1 mb-3'>{giftCardData.rate}</div>
                                        </Col>

                                        <Col md={6}>
                                            <div className='mb-1 border-bottom'><b>Discount Type</b></div>
                                            <div className='mt-1 mb-3'>{giftCardData.discount_type}</div>
                                        </Col>
                                    </Row>

                                    <Row>

                                        <Col md={6}>
                                            <div className='mb-1 border-bottom'><b>Available Usages</b></div>
                                            <div className='mt-1 mb-3'>{giftCardData.count_limit}</div>
                                        </Col>
                                        <Col md={6}>
                                            <div className='mb-1 border-bottom'><b>Used</b></div>
                                            <div className='mt-1 mb-3'>{giftCardData.count_used} times</div>
                                        </Col>
                                    </Row>







                                </Col>
                                <Col md={2}></Col>



                            </div>



                        </Card.Body>

                        <Card.Body >
                            <Card.Header >
                                <Card.Title as="h5" style={{ marginTop: '5px' }} >Unique Cards</Card.Title>
                            </Card.Header>
                            <Table striped responsive>
                                <thead>
                                    <tr>
                                        <th>#</th>
                                        <th>Card Code</th>
                                        <th>Used</th>
                                        <th>Enabled</th>
                                        <th></th>
                                    </tr>
                                </thead>
                                <tbody>
                                    {UniqueCardsList || ''}
                                </tbody>
                            </Table>
                            {isEmpty(giftCardData.unique_cards) && !loading ? <><IsEmpty>Data not available</IsEmpty><br /><br /><br /></> : ''}
                        </Card.Body>
                        {loading ? <Spinner /> : null}
                        {/* <Card.Footer>
                            <Paging />
                        </Card.Footer> */}
                    </Card>
                </Col>
                <Col xl={1}></Col>
                <DeleteModal
                    show={deleteGiftCardData.showModal}
                    onCancel={() => setDeleteGiftCardData({ ...deleteGiftCardData, showModal: false })}
                    onDelete={() => _deleteGiftCard(deleteGiftCardData.giftCardUuid)}
                />
                <DeleteModal
                    show={deleteUniqueCardData.showModal}
                    onCancel={() => setDeleteUniqueCardData({ ...deleteUniqueCardData, showModal: false })}
                    onDelete={() => _deleteUniqueCard(deleteUniqueCardData.uniqueCardUuid)}
                />
            </Row>
        </>
    );
}

export default GiftCard;
